*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
	margin: 0;
	font-family: "Montserrat", sans-serif;
	line-height: 1.33;
}

header,
main,
footer {
	max-width: 75%;
	margin-left: auto;
	margin-right: auto;
}

.title {
	width: 100%;
	font-family: "Playfair Display", serif;
	font-size: 3rem;
	font-weight: 900;
}
